.product_card{
  max-width: 300px;
  overflow: hidden;
  height: 500px;
  padding: 15px;
  box-shadow: 0 0 15px #ddd;
  margin: 10px 0;
  position: relative;
  border-radius: 15px;
}
.product_card img{
  width: 100%;
  height: 300px;
  display: block;
  object-fit: cover;
  border-radius: 10px;
}
.product_box h2{
  width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  text-transform: capitalize;
  cursor: pointer;
  color: #323232;
}
.product_card span{
  color: rgb(34, 62, 190);
  font-weight: 700;
  font-size: 20px;
}
.product_box p{
  width: 100%;
  display: -webkit-box;
  font-weight: 700;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  height: 70px;
  overflow: hidden;
  color: #323232;
}

.row_btn{
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.row_btn a{
margin-top: -2.5px;
  width: 50%;
  text-align: center;
  text-transform: uppercase;
  color: white;
  font-weight: 600;
  letter-spacing: 2px;
  padding: 6px;
  
}
#btn_buy{
  background: #555;
  margin-left: 2%;
  margin-right: 2%;
  padding-top: 2%;
  padding-bottom: 2%;
  transition: 0.7s ease;
  border-radius: 7px;
}
#btn_buy:hover{
transform: scale(1.1);
background: rgb(104, 103, 103);
}
#btn_view{
  background: teal;
  margin-left: 2%;
  margin-right: 2%;
  padding-top: 2%;
  padding-bottom: 2%;
  transition: 0.7s ease;
  border-radius: 7px;
}
#btn_view:hover{
transform: scale(1.1);
background: rgba(2, 172, 172, 0.767);
}
.product_card input{
  position: absolute;
  width: 25px;
  height: 25px;
}