.detail{
  width: 75%;
  height: 90%;
  margin-left: 12.5%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 50px;
  box-shadow: 2 2 rgb(104, 103, 103);
  font-size: 150%;
}
.detail img{
  max-width: 150px;
  width: 100%;
  margin: 20px;
  height: 250px;
  object-fit: cover;
  display: block;
  border-radius: 15px;
  
}
.box-detail{
  box-shadow: 2 2 rgb(104, 103, 103);
  max-width: 500px;
  width: 100%;
  margin: 5px 20px;
}
.box-detail .row{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.box-detail h2{
  text-transform: uppercase;
  color: darkblue;
  letter-spacing: 2px;
  font-weight: 2rem;
}
.box-detail p{
  line-height: 1.5;
  margin: 10px 0;
  opacity: 0.8;
}
.box-detail .cart{
  background: #333;
  color: white;
  margin-top: 10px;
  padding: 10px 25px;
  display: inline-block;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: 0.7s ease;
  border-radius: 7px;
}
.box-detail .cart:hover{
transform: scale(1.1);
background: rgb(104, 103, 103);
}